.App {
 text-align: center;
 font-family: 'Raleway', sans-serif;
}

.title-head {
  padding: 20px;
  font-weight: 700;
}

.subtitle {
  padding-bottom: 10px;
}
.recipes {
  display: grid;
  text-align: center;
  grid-template-columns: 2fr 2fr 2fr;
  grid-template-rows: 3;
}

.image {
  margin: 0 auto;
}

a:link {
  color: white;
}

/* visited link */
a:visited {
  color: white;
}

/* mouse over link */
a:hover {
  color:yellow;
}

/* selected link */
a:active {
  color: yellow;
}

@media only screen and (max-width: 375px) and (min-width: 200px) {
  .recipes {
    display: grid;
    text-align: center;
    grid-template-columns: 2fr;
    grid-template-rows: 3;
    align-content: center;
  }
}


@media only screen and (min-width: 501px) and (max-width: 1030px) {
  .recipes {
    display: grid;
    text-align: center;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 3;
    align-content: center;
  }
  
}