.contact-form {
    display: grid;
    text-align: center; 
    font-family: 'Raleway', sans-serif;
}

.banner {
    width: 100%;
}

h1{
    font-weight: 900;
    padding-bottom: 10px;
}

.contact-text {
    padding: 20px;
    font-weight: 700;
}

.medium-text {
    color:rgba(16, 16, 237, 0.659);
}

.small-text {
    color:rgb(20, 191, 191);
}

.form-part {
    width: 40%;
    text-align: center;
    margin: 0 auto;
    padding: 20px;
}


@media only screen and (max-width: 375px) and (min-width: 200px) {
    .banner {
        width: 100%;
    }
    .form-part {
        width: 70%;
        text-align: center;
        margin: 0 auto;
        padding: 20px;
    }
    h1{
        font-weight: 900;
        padding-bottom: 10px;
    }
    
    .contact-text {
        padding: 20px;
        font-weight: 700;
    }
    
    .medium-text {
        display: none;
    }
    
    .small-text {
        color:rgb(20, 191, 191);
    }
  }
  
  
  @media only screen and (min-width: 501px) and (max-width: 1030px) {
    .banner {
        width: 100%;
    }
    .form-part {
        width: 70%;
        text-align: center;
        margin: 0 auto;
        padding: 20px;
    }
    
  }