.recipelist {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: grid;
    justify-content: space-evenly;
    background-color:black;
    color: white;
    align-items: center;
    min-width: 80%;
    padding-bottom: 30px;
  }
  
  .image {
    border-radius: 50%;
    width: 250px;
    height: 250px;
    padding: 20px;
    align-content: center;
  }